* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.container {
  width: 100vw;
  height: 100px;
  background-color: purple;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.search-form {
  width: 40%;
  height: 100%;
  position: relative;
}

.search-form__input {
  width: 100%;
  height: 100%;
  border-width: 0;
  border-radius: 5px;
  padding-right: 10%;
  font-size: 200%;
}

.search-form__input:focus {
  outline: 3px solid #000;
}

.button {
  min-width: 40px;
  width: 10%;
  height: 100%;
  border-width: 0;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.button__img {
  width: 50%;
  height: 50%;
}

